@import url(https://fonts.googleapis.com/css2?family=Open+Sans&display=swap);
*:root{
    font-size: 16px;
    font-family: 'Open Sans';
    --bg-color:hsl(230, 5%, 12%);
}

body{
    background-color: var(--bg-color) ;
    color:white;
    height: 100vh;
    width: 100vw;
    margin: 0;
    padding: 0;
    overflow-x: hidden;
}

#root{
    height: 100%;
    width: 100%;
    margin: 0;
    padding: 0;
}

.App{
    display: flex;
    justify-content: flex-start;
    flex-direction: column;
    align-items: center;
    width: 100%;
    height: 100%;
    padding-top: 100px;
    box-sizing: border-box;
}

.container{
    max-width: 1200px;
    width:100%;
    height: inherit;
    margin: auto;
}

.holder{
    max-width: 800px;
    height: auto;
    border: 2px solid transparent;
    border-radius: 15px;
    padding: 2.5em 5em;
    transition: border-color 0.8s ease-out;
    box-shadow: 0 8px 15px 1px #0000006e;

}

.holder:hover{
    /* border: 2px solid white */
}

.md-size{
    max-width: 300px;
    width: auto;
    height: auto;
    margin: auto;
}

ul > li {
    margin-top: 2em;
    
}

.btn{
    text-decoration: none;
    padding: 0.5em 0.75em;
    background-color: transparent;
    border: 3px solid #4084e9c2;
    border-radius: 8px;
    color:#4084e9c2;
    font-weight: 500;
    letter-spacing: 2px;
    font-size: 0.75rem;
    box-shadow: 0px 5px 8px 0px #0000006e;
    outline: none;
    transition: all 0.3s ease-out;
    transition: border-color 0.1s ease-out;
    margin: 1.5em;
}

.btn:hover{
    color: black;
    background-color: #4084e9c2;
}

.btn:active{
    border-color: #90b9f7f5;
    outline: none;
}

.btn:focus{
    color: black;
    background-color: #4084e9c2;
}

.btn-lg{
    font-size: 18px;
}

.btn--no-outline{
    border-color: transparent !important;
}

.table__row{
    height: auto;
    background-color: hsl(230, 5%, 12%);
    border: 2px solid transparent;
    border-radius: 15px;
    margin: 2em auto;
    padding: 0.75em 1.25em;
    transition: background-color 0.3s ease-out;
    box-shadow: 0 8px 15px 1px #0000006e;
}

.table__row:hover{
    background-color: hsl(230, 5%, 10%);
    /* border: 2px solid white */
}

.navbar{
    margin-bottom: 2em;
}

.top{
    height: auto;
    box-sizing: border-box;
    display: inline-flex;
}

.column{
    display: block;
    min-width:550px;
}


input {
    -webkit-appearance: none;
    width: 100%;
    border: 0;
    border-bottom: 2px solid #ccced3;
    padding-left: 0.5em;
    font-family: inherit;
    height: 48px;
    font-size: 18px;
    font-weight: 500;
    background: none;
    border-radius: 0;
    color: #ccced3;
    transition: all 0.15s ease;
    margin:auto;
    box-sizing: border-box;
    margin-bottom: 2em;
    border-radius: 8px;
}

input.btn{
    margin:auto;
    margin-top: 2em;
}

input:focus{
    outline: none;
    border-color: #2f82ff;
    box-shadow: 0 2px 4px 5px #2f82ff3e;
}

input::-webkit-input-placeholder {
    color: #9098a9;
}

input:-ms-input-placeholder {
    color: #9098a9;
}

input::-ms-input-placeholder {
    color: #9098a9;
}

input::placeholder {
    color: #9098a9;
}
.navbar{
    position: absolute;
    top:0;
    height: 65px;
    box-shadow: 0 5px 15px 1px #0000006e;
    width: 100vw;
    z-index: 2;
    
}

.nav{
    display: flex;
    width: 100%;
    justify-content: space-between;
    height: inherit;
}

.brand{
    width: auto;
    margin: auto 1em;
    float: left;
}

.brand-img{
    height:auto;
    width: 100%;
    display: block;
    max-width: 70px;
    cursor: pointer;

}

.navbar-content{
    width: auto;
    height: 100%;
    margin: auto 1em;
    display: inherit;
}

.navbar-btn{
    height: 100%;
    border: 1px solid transparent;
    width: 100%;
    padding: 1em;
    display: block;
    box-sizing: border-box;
    text-align: center;
    white-space: nowrap;
    cursor: pointer;
    position: relative;
    transition: all 0.2s ease-in;
    top:0;
    background-color: var(--bg-color) ;

}

a[href]{
    color:inherit !important;
    text-decoration: none !important;
}

.navbar-btn:hover{
    border: 1px solid #ffffffea;
    border-bottom: 4px solid #ffffffea;
    top:5px;
}


.error{
    background-color: rgba(255, 74, 74, 0.534);
    border: 2px solid rgb(255, 74, 74);
    padding:0.5em 1em;
    border-radius: 10px;
}


.success{
    background-color: rgba(74,255, 74, 0.534);
    border: 2px solid rgb(74,255, 74);
    padding:0.5em 1em;
    border-radius: 10px;
}

.lds-dual-ring {
    display: inline-block;
    width: 80px;
    height: 80px;
  }
.lds-dual-ring:after {
    content: " ";
    display: block;
    width: 64px;
    height: 64px;
    margin: 8px;
    border-radius: 50%;
    border: 6px solid #fff;
    border-color: #fff transparent #fff transparent;
    -webkit-animation: lds-dual-ring 1.2s linear infinite;
            animation: lds-dual-ring 1.2s linear infinite;
}
@-webkit-keyframes lds-dual-ring {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
}
@keyframes lds-dual-ring {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
}
  
.post__image{
    width:100%;
    height:auto;
}
